import { useState } from "react";
import {
    DataGrid,
    GridColDef,
    GridValueGetterParams,
} from "@material-ui/data-grid";
import { IconButton } from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import { movingAvgItem } from "../../modules/movingAvg";
import { utils } from "../../utils/utils";

interface Props {
    items: movingAvgItem[];
    onRemove(selectedItems: any[]): void;
}

const MovingAverageTable = ({ items, onRemove }: Props) => {
    const [selectionModel, setSelectionModel] = useState<any[]>([]);

    const columns: GridColDef[] = [
        { field: "id", headerName: "ID", width: 100 },
        {
            field: "times",
            headerName: "",
            width: 150,
            editable: true,
            valueGetter: (params: GridValueGetterParams) =>
                `${utils.getEnumIntervalByValue(
                    params.getValue(params.id, "period")?.toString() || ""
                )}`,
        },
        // {
        //     field: "findCount",
        //     headerName: " ",
        //     // type: "number",
        //     width: 150,
        //     editable: true,
        // },
        {
            field: "condition",
            headerName: "  ",
            description: "    .",
            sortable: false,
            width: 500,
            editable: false,
            valueGetter: (params: GridValueGetterParams) =>
                `[${params.getValue(params.id, "period") || ""}]   : 
                 (${params.getValue(
                     params.id,
                     "findCount" || ""
                 )} ) ${
                    params.getValue(params.id, "compareCond") === ""
                        ? ">="
                        : "<="
                } (${params.getValue(params.id, "compareVal")} )`,
        },
        {
            field: "delete",
            width: 75,
            sortable: false,
            disableColumnMenu: true,
            renderHeader: () => {
                return (
                    <IconButton
                        onClick={() => {
                            onRemove(selectionModel);
                        }}
                    >
                        <DeleteIcon />
                    </IconButton>
                );
            },
        },
    ];

    return (
        <div style={{ height: 400, width: "100%" }}>
            <DataGrid
                rows={items}
                columns={columns}
                pageSize={5}
                checkboxSelection
                disableSelectionOnClick
                onSelectionModelChange={(ids) => {
                    setSelectionModel(ids);
                }}
                // style={{ borderColor: "#2b6777" }}
            />
        </div>
    );
};

export default MovingAverageTable;
